* {
  font-family: "Courier New", Courier, monospace;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.anchor {
  scroll-margin-top: 64px;
}

.text-shadow {
  text-shadow: 1px 1px 3px rgba(#fffdd0, 1);
  transition: text-shadow 0.05s ease-in;
  &:hover {
    text-shadow: 1px 1px 3px rgba(#000, 0.4);
  }
}

nav {
  position: sticky;
  top: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  background-image: linear-gradient(white 85%, transparent);
  z-index: 6;


  .logo {
    height: 60px;
    width: 60px;
    margin-left: 30px;
  }

  .links {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 80%;
  }
}

.gradient-white-trans_vert {
  background-image: linear-gradient(white, transparent);
  height: 20px;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;

  .container {
    margin-top: 20px;
  }
}

#visual-slam {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
  max-width: 420px;

  .desc {
    max-width: 200px;

    span {
      text-shadow: 1px 1px 3px rgba(#fffdd0, 1);
      vertical-align: middle;
    }
  }
}

#whoami {
  display: flex;
  flex-direction: row;

  .desc {
    max-width: 200px;
  }

  .boyowa {
    width: 200px;
    height: 250px;
    background-color: blanchedalmond;
  }
}

#connect {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  width: 300px;
  margin-bottom: 20px;

  form {
    display: flex;
    flex-direction: column;
  }

  .socials {
    display: flex;
    justify-content: flex-start;

    .icon {
      width: 30px;
      margin: 0 10px;
    }
  }
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  color: whitesmoke;
  background-color: rgb(26, 0, 0);

  #year {
    margin: 0 3px;
  }
}

@media only screen and (max-width: 620px) {
  nav {
    .logo {
      margin-left: 10px;
    }
  }

  #visual-slam {
    flex-direction: column;
    align-items: center;

    .desc {
      margin-top: 15px;
    }
  }
}
